.map {
  width: 100%;
  height: 350px;
  background-image: url("/images/index/map-marker.svg.svg"), url("/images/index/map-mobile.jpg");
  background-position: center, center;
  background-repeat: no-repeat, no-repeat;
  background-size: 27px 27px, cover;

  @media (min-width: @tablet-width) {
    background-image: url("/images/index/map-marker.svg.svg"), url("/images/index/map-tablet.jpg");
  }

  @media (min-width: @desktop-width) {
    background-image: url("/images/index/map-marker.svg.svg"), url("/images/index/map-desktop.jpg");
  }
}
